import React from "react";

export const WhichFormContext = React.createContext<whichFormType | null>(null);

export const WhichFormProvider: React.FC<React.ReactNode> = ({ children }) => {
  const [whichForm, setWForm] = React.useState<boolean>(true);

  const showWhichForm = (whichForm: boolean) => {
    setWForm(whichForm);
  };
  return (
    <WhichFormContext.Provider value={{ whichForm, showWhichForm }}>
      {children}
    </WhichFormContext.Provider>
  );
};

export default WhichFormProvider;
