export const validateInput = (
  input: string
): { show: boolean; message: string } => {
  if (!input) {
    return {
      show: true,
      message: "Please enter a value.",
    };
  }

  if (!(input.length >= 6 && input.length <= 20)) {
    return {
      show: true,
      message: "Please enter a value.",
    };
  }

  return {
    show: false,
    message: "",
  };
};
