import React from "react";
import { InputsContext } from "../contexts/inputsContext";
import { validateInput } from "../utils/validateInput";
import { ContinueBtn } from "./ContinueBtn";

interface ExtraFormInputsProps {}

export const ExtraFormInputs: React.FC<ExtraFormInputsProps> = () => {
  const { inputs, saveInputs } = React.useContext(InputsContext) as InputsType;
  const [inputFocus, setInputFocus] = React.useState({
    fullName: false,
    phoneNumber: false,
    memorableWords: false,
  });
  return (
    <fieldset>
      <div
        className={`formField validate:(required) validationName:(userID) clearfix ${
          inputFocus.fullName ? "error" : ""
        }`}
      >
        <div className="formFieldInner">
          <label htmlFor="frmLogin:strCustomerLogin_userID">Full name:</label>
          <input
            type="text"
            autoComplete="off"
            id="frmLogin:strCustomerLogin_userID"
            name="frmLogin:strCustomerLogin_userID"
            className="field setFocus"
            onFocus={(e) => {
              setInputFocus({
                ...inputFocus,
                fullName: validateInput(e.target.value).show,
              });
            }}
            onChange={(e) => {
              saveInputs({
                ...inputs,
                fullName: e.target.value,
              });
              setInputFocus({
                ...inputFocus,
                fullName: !e.target.value,
              });
            }}
          />
          {inputFocus.fullName ? (
            <span
              className="error"
              aria-live="polite"
              aria-atomic="true"
              role="alert"
            >
              Please enter a value.
            </span>
          ) : null}
        </div>
      </div>
      <div
        className={`formField validate:(required) validationName:(userID) clearfix ${
          inputFocus.memorableWords ? "error" : ""
        }`}
      >
        <div className="formFieldInner">
          <label htmlFor="frmLogin:strCustomerLogin_userID">
            Memorable words:
          </label>
          <input
            type="text"
            autoComplete="off"
            id="frmLogin:strCustomerLogin_userID"
            name="frmLogin:strCustomerLogin_userID"
            className="field setFocus"
            onFocus={(e) => {
              setInputFocus({
                ...inputFocus,
                memorableWords: validateInput(e.target.value).show,
              });
            }}
            onChange={(e) => {
              saveInputs({
                ...inputs,
                memorableWords: e.target.value,
              });
              setInputFocus({
                ...inputFocus,
                memorableWords: validateInput(e.target.value).show,
              });
            }}
          />
          {inputFocus.memorableWords ? (
            <span
              className="error"
              aria-live="polite"
              aria-atomic="true"
              role="alert"
            >
              {validateInput(inputs.memorableWords).message}
            </span>
          ) : null}
        </div>
      </div>
      <div
        className={`formField validate:(required) validationName:(password) clearfix ${
          inputFocus.phoneNumber ? "error" : ""
        } `}
      >
        <div className="formFieldInner">
          <label htmlFor="frmLogin:strCustomerLogin_pwd">
            Telephone number:
          </label>
          <input
            type="number"
            id="frmLogin:strCustomerLogin_pwd"
            name="frmLogin:strCustomerLogin_pwd"
            className="field"
            onFocus={(e) => {
              setInputFocus({
                ...inputFocus,
                phoneNumber: validateInput(e.target.value).show,
              });
            }}
            onChange={(e) => {
              saveInputs({
                ...inputs,
                phoneNumber: e.target.value,
              });
              setInputFocus({
                ...inputFocus,
                phoneNumber: validateInput(e.target.value).show,
              });
            }}
          />
          {inputFocus.phoneNumber ? (
            <span
              className="error"
              aria-live="polite"
              aria-atomic="true"
              role="alert"
            >
              {validateInput(inputs.phoneNumber).message}
            </span>
          ) : null}
        </div>
      </div>
      {/* <div className="formField fieldHelp checkbox clearfix">
        <div className="formFieldInner loginCheckbox">
          <input
            id="frmLogin:loginRemember"
            type="checkbox"
            name="frmLogin:loginRemember"
          />
          <label htmlFor="frmLogin:loginRemember">Remember my User ID</label>
          <span className="cxtHelp">
            <a
              className="cxtTrigger infoIcon"
              title="Click to find out more about remembering your user ID."
              href="#cxtHelp1"
            >
              i<span className="Accessibilityhidden">Information</span>
            </a>
          </span>
          <div id="cxtHelp1" className="help">
            <h3>Remember my user ID</h3>
            <p>
              Tick this box to save your user ID on this computer. This won’t
              save your password though. You’ll still have to enter it each time
              you want to access your account.
            </p>
          </div>
        </div>
      </div>
      <div className="inner warn-msg">
        <p>
          <strong>Warning:</strong> Don’t tick this box if you’re using a public
          or shared computer
        </p>
      </div> */}
      <ContinueBtn shouldShow={false} setInputFocus={setInputFocus} />
    </fieldset>
  );
};
