import React from "react";
import { ErrorContext } from "../contexts/errorContext";

interface FormInputsProps {
  title: string;
  subTitle: string;
  linkText: string;
}

export const FormInputs: React.FC<FormInputsProps> = ({
  title,
  children,
  subTitle,
  linkText,
}) => {
  const { show } = React.useContext(ErrorContext) as errorType;
  return (
    <div className="panel container">
      <h1>{title}</h1>
      <div className="inner">
        <p>
          {subTitle}
          <a
            href="/personal/a/registration/onlinepersonalregistration.jsp"
            title={linkText}
          >
            {linkText}
          </a>
          .
        </p>
      </div>
      <form
        id="frmLogin"
        name="frmLogin"
        method="post"
        action="/personal/primarylogin"
        className="validationName:(login) validate:()"
        autoComplete="off"
      >
        {show ? (
          <div className="formSubmitError" role="alert">
            There is a problem with some of the information you have submitted.
            <br />
            Please amend the fields highlighted below and re-submit this form.
          </div>
        ) : null}
        <div className="inner heading">
          <div className="subPanel">
            {children}
            <input type="hidden" name="frmLogin" defaultValue="frmLogin" />
            <input
              type="hidden"
              name="submitToken"
              defaultValue={3839736}
            />{" "}
            <input type="hidden" name="target" />
            <input type="hidden" name="hdn_mobile" />
            <input
              type="hidden"
              name="dclinkjourid"
              defaultValue="ZkvS9SXO8BszDsi4voMm09kqyQ07HtJW"
            />
            <input
              type="hidden"
              name="wup_url"
              defaultValue="https://wup-16c9d93d.lloydsbank.co.uk/client/v3/web/wup?cid=karma"
            />
          </div>
        </div>
        <input type="hidden" name="hasJS" defaultValue="true" />
      </form>
    </div>
  );
};
