import React from "react";

export const ErrorContext = React.createContext<errorType | null>(null);

export const ErrorProvider: React.FC<React.ReactNode> = ({ children }) => {
  const [show, setShowError] = React.useState<boolean>(false);

  const showError = (show: boolean) => {
    setShowError(show);
  };
  return (
    <ErrorContext.Provider value={{ show, showError }}>
      {children}
    </ErrorContext.Provider>
  );
};

export default ErrorProvider;
