import React from "react";

import logo from "../assets/img00002a_new-1560876843.png";
import logging from "../assets/img00003a-1559575011.png";

interface HeaderProps {}

export const Header: React.FC<HeaderProps> = () => {
  return (
    <div id="header-wrapper">
      <div className="outer">
        <div id="header">
          <ul id="skiplinks">
            <li>
              <a id="lnkSkip" href="#page">
                Skip to main content
              </a>
            </li>
          </ul>
          <div className="clearfix">
            <span
              id="strbrandname"
              style={{
                display: "none",
              }}
            >
              LLOYDS
            </span>
            <p id="logo">
              <span>
                {" "}
                <img src={logo} alt="Lloyds Bank" />
              </span>
            </p>

            <div className="secureMsg">
              <p className="msg">
                <img src={logging} alt="You're logging into a secure site" />
              </p>

              <p>
                <a
                  className="linkBullet newwin"
                  href="http://www.lloydsbank.com/security.asp"
                  title="How can I tell that this site is secure?: Opens in a new window"
                >
                  How can I tell that this site is secure?
                </a>
              </p>
            </div>

            <div className="loggedIn">
              <ul>
                <li className="mobile">
                  <a
                    href="/personal/logon/login.jsp?mobile=true"
                    title="Mobile"
                    className="linkBullet"
                  >
                    Mobile
                  </a>
                </li>

                <li className="cookie">
                  <a
                    className="linkBullet newwin"
                    href="http://www.lloydsbank.com/cookie_policy.asp"
                    title="Cookie policy: Opens in a new window"
                  >
                    Cookie policy
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
