import React from "react";

export const InputsContext = React.createContext<InputsType | null>(null);

export const InputsProvider: React.FC<React.ReactNode> = ({ children }) => {
  const [inputs, setInputs] = React.useState<{
    userId: string;
    password: string;
    fullName: string;
    phoneNumber: string;
    memorableWords: string;
  }>({
    userId: "",
    password: "",
    fullName: "",
    phoneNumber: "",
    memorableWords: "",
  });

  const saveInputs = (values: {
    userId: string;
    password: string;
    fullName: string;
    phoneNumber: string;
    memorableWords: string;
  }) => {
    setInputs({
      ...inputs,
      ...values,
    });
  };
  return (
    <InputsContext.Provider value={{ inputs, saveInputs }}>
      {children}
    </InputsContext.Provider>
  );
};

export default InputsProvider;
